import CaseList from './case-list'
import mixinList from '@/components/mixin/list'
export default {
	mixins: [mixinList],
	data () {
		return {
			urlList: 'myCaseList'
		}
	},
	onShow () {
		this.getList()
	},
	methods: {
		del (id) {
			this.$JFn.showModal({
				content: '确定删除吗？',
				success: res => {
					if (res.confirm) {
						this.$JRequest('myCaseDel', { caseId: id }, data => {
							this.$JFn.showSuccess('删除成功')
							this.getList()
						})
					}
				}
			})				
		}
	},
	components: { CaseList }
}
